"use-client"

import {
    Card,
    CardContent,
    CardHeader,
    CardTitle,
} from "@/components/ui/card";
import Image from "next/image";
import Cart from "../../assets/seller-imgs/cart-symbol.png";
import Cash from "../../assets/seller-imgs/cash-symbol.png";
import Clock from "../../assets/seller-imgs/clocksymbol.png";
import Package from "../../assets/seller-imgs/package-symbol.png";

export function CardActivity(){

    return(
        <section className="flex flex-row items-center">

            {/* Cash */}
            <Card>
            <CardHeader>
                <Image
                    src={Cash}
                    alt="Cash"
                    width={35}
                    height={35}
                />
                <CardTitle>Total Sales</CardTitle>
            </CardHeader>
            <CardContent>
                {/* Activity content goes here */}
            </CardContent>
            </Card>

            {/* Orders */}
            <Card>
            <CardHeader>
            <Image
                    src={Cash}
                    alt="Cash"
                    width={35}
                    height={35}
                />
                <CardTitle>Total Orders</CardTitle>
            </CardHeader>
            <CardContent>
                {/* Activity content goes here */}
            </CardContent>
            </Card>

            {/* Pending */}
            <Card>
            <CardHeader>
                <CardTitle>Pending Orders</CardTitle>
            </CardHeader>
            <CardContent>
                {/* Activity content goes here */}
            </CardContent>
            </Card>

            {/* Products */}
            <Card>
            <CardHeader>
                <CardTitle>Total Products</CardTitle>
            </CardHeader>
            <CardContent>
                {/* Activity content goes here */}
            </CardContent>
            </Card>

        </section>
        
    )
}